.object-fit{
	&-cover{
		width: 100%;
		height: 100%;
		position: relative;
		object-fit:cover;
	}
	&-contain{
		width: 100%;
		height: 100%;
		position: relative;
		object-fit:contain;
	}
}

.h-image-cover{
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	object-fit: cover;
}